
.board {
  height: 100%;
}
.board ul.content-menu {
    width: 200px;
    background-color: #fff;
    position: absolute;
    padding: 0;
    box-sizing: border-box;
    box-shadow: 0 0 0 1px rgba(0, 0, 0, 0.15), 0 8px 16px rgba(0, 0, 0, 0.15);
}
.board ul.content-menu li {
      padding: 15px;
      width: 100%;
      display: block;
      list-style-type: none;
      box-sizing: border-box;
      border-bottom: 1px solid #eee;
}
.board ul.content-menu li i {
        margin-right: 10px;
}
.board ul.content-menu li.disabled {
        color: #ccc;
}
.board ul.content-menu li:hover {
      background-color: rgba(1, 1, 1, 0.1);
}
.actions {
  position: relative;
  width: 100%;
  border-top: 1px solid #eee;
  border-bottom: 1px solid #eee;
  background-color: #fff;
  display: -ms-flexbox;
  display: flex;
}
.actions .tools {
    box-sizing: border-box;
    padding: 0 20px;
    border-right: 1px solid #ddd;
}
.actions .tools ul {
      display: -ms-flexbox;
      display: flex;
      margin: 0;
      padding: 0;
}
.actions .tools ul li {
        list-style-type: none;
        padding: 15px;
        height: 54px;
        text-align: center;
        box-sizing: border-box;
        cursor: pointer;
        position: relative;
}
.actions .tools ul li .iconfont {
          font-size: 18px;
}
.actions .tools ul li .plugin-tools-item-action {
          position: absolute;
          top: 40px;
          padding: 10px;
          top: 58px;
          background-color: #fff;
          border-radius: 4px;
          box-shadow: 0 0 0 1px rgba(0, 0, 0, 0.15), 0 8px 16px rgba(0, 0, 0, 0.15);
          z-index: 12;
}
.actions .tools ul li:hover {
          background-color: #eee;
}
.actions .tools ul li.selected {
          background-color: #eee;
}
.actions .tools ul li i {
          font-size: 16px;
}
.actions .tools ul li i.disabled {
            color: #ccc;
}
.actions .tools.props ul li {
      display: -ms-flexbox;
      display: flex;
      -ms-flex-align: center;
          align-items: center;
}
.actions .tools.props ul li:hover {
        background-color: #fff;
}
.actions .tools.props ul li.selected {
        background-color: #fff;
}
.actions .tools.props ul li .el-input--suffix .el-input__inner {
        padding-right: 0 !important;
}
.actions .tools.props ul li .el-input {
        width: 63px;
}
.actions .tools.props ul li .el-input .el-input__inner {
          border: 0;
}
.canvas-container {
  height: 100%;
}
.canvas-container.eraser canvas {
    cursor: none !important;
}
.canvas-container.choose canvas {
    cursor: initial;
}
.canvas-container canvas {
    width: 500px;
    height: 600px;
}
.canvas-container canvas#layer-draw {
    background-color: #fff;
}
