
.color-picker {
  position: relative;
}
.color-picker .color-preview {
    margin-top: 1px;
    -ms-flex-align: center;
        align-items: center;
}
.color-picker .color-preview > .value {
      width: 100px;
      height: 20px;
      border-radius: 5px;
}
.color-picker .fade-enter-active {
    transition: all .3s ease;
}
.color-picker .fade-leave-active {
    transition: all 0.3s cubic-bezier(1, 0.5, 0.8, 1);
}
.color-picker .fade-enter, .color-picker .fade-leave-to {
    opacity: 0;
}
.color-picker > .color-picker-main {
    position: absolute;
    z-index: 12;
    top: 40px;
}
