
.action-upload-img {
  cursor: initial;
  width: 200px;
  box-sizing: border-box;
}
.action-upload-img .drag-area {
    cursor: pointer;
    height: 180px;
    line-height: 180px;
    text-align: center;
    border: 1px dashed #ccc;
    background-color: #eee;
    color: #aaa;
    margin-bottom: 20px;
}
.action-upload-img .drag-area.hover {
      border-color: #67c23a;
}
.action-upload-img .drag-area > img {
      max-width: 100%;
      height: 180px;
}
